.plusup-grid {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap; }
  .plusup-grid .plusup-grid-head {
    padding: 1rem; }
    @media (max-width: 419px) {
      .plusup-grid .plusup-grid-head {
        width: 100%;
        padding: 0.4rem; } }
    .plusup-grid .plusup-grid-head .plusup-where {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
      height: 10rem; }
      .plusup-grid .plusup-grid-head .plusup-where .armmor-dv {
        display: grid;
        grid-template-columns: repeat(3, 1fr); }
      .plusup-grid .plusup-grid-head .plusup-where .grade-dv {
        display: grid;
        grid-template-columns: repeat(3, 1fr); }
      .plusup-grid .plusup-grid-head .plusup-where .tier-dv {
        display: grid;
        grid-template-columns: repeat(3, 1fr); }
  .plusup-grid .plus-up-grid-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 0.8rem;
    padding: 1rem; }
    @media (max-width: 419px) {
      .plusup-grid .plus-up-grid-list {
        width: 100%;
        padding: 0.4rem; } }
    .plusup-grid .plus-up-grid-list .plus-up-grid-object {
      font-weight: bold; }
      .plusup-grid .plus-up-grid-list .plus-up-grid-object .plus-up-text {
        display: flex; }
        .plusup-grid .plus-up-grid-list .plus-up-grid-object .plus-up-text .plus-up-grade {
          width: 36px;
          color: #212529; }
        .plusup-grid .plus-up-grid-list .plus-up-grid-object .plus-up-text .plus-up-value {
          width: 54px;
          color: #c92a2a; }
  @media (max-width: 1399px) {
    .plusup-grid .plusup-grid-adsense {
      margin: auto; } }
  @media (max-width: 419px) {
    .plusup-grid .plusup-grid-adsense .ad300250 {
      margin-right: 0.2rem;
      display: none !important; } }
  .plusup-grid .plusup-grid-adsense .ad72890 {
    display: none !important; }
    @media (max-width: 419px) {
      .plusup-grid .plusup-grid-adsense .ad72890 {
        margin-right: 0.2rem;
        display: inline-block !important; } }
